import React, { useRef, useState } from "react";
import { Box, Paper, Typography, TextField, Button } from "@mui/material";
import GoogleMapReact, {
  ClickEventValue,
  ChangeEventValue,
} from "google-map-react";
import {
  // ConvertToCoordinatesClient,
  // ConvertToCoordinatesOptions,
  ConvertTo3waClient,
  ConvertTo3waOptions,
  GridSectionClient,
  GridSectionOptions,
  GridSectionJsonResponse,
  GridSectionGeoJsonResponse,
} from "@what3words/api";
import { parsePosition } from "utils/convert";
import gridLineData from "config/gridLineData";

const API_KEY = "TJB3XFA5";

const GoogleMap = () => {
  const mapRef = useRef<any>(null);
  const [currentPositionWhat3words, setCurrentPositionWhat3words] =
    useState<string>("");

  const [searchNumber, setSearchNumber] = useState<string>("");
  const [convertLoading, setConvertLoading] = useState<boolean>(false);
  // const [rectArea, setRectArea] = useState<any>(undefined);
  const [mapNumber, setMapNumber] = useState<number | null>(null);
  const [marker, setMarker] = useState<any>(undefined);

  const [selectNftPositionTop, setSelectNftPositionTop] = useState<number>(0);
  const [selectNftPositionLeft, setSelectNftPositionLeft] = useState<number>(0);
  const [nftBoundWords, setNftBoundWords] = useState<{
    topLeftWhat3Words: string;
    topRightWhat3Words: string;
    bottomLeftWhat3Words: string;
    bottomRightWhat3Words: string;
  }>({
    topLeftWhat3Words: "",
    topRightWhat3Words: "",
    bottomLeftWhat3Words: "",
    bottomRightWhat3Words: "",
  });
  let gridData: any;
  // let rectangle: any = undefined;

  const gridClient: GridSectionClient = GridSectionClient.init(API_KEY);
  const convertToWordsClient: ConvertTo3waClient =
    ConvertTo3waClient.init(API_KEY);
  // const convertToDmsClient: ConvertToCoordinatesClient =
  //   ConvertToCoordinatesClient.init(API_KEY);

  const resetNftData = () => {
    setMapNumber(null);
    setNftBoundWords({
      topLeftWhat3Words: "",
      topRightWhat3Words: "",
      bottomLeftWhat3Words: "",
      bottomRightWhat3Words: "",
    });
  };

  const distanceInPx = (
    marker1: { lat: number; lng: number },
    marker2: { lat: number; lng: number }
  ) => {
    const point1 = mapRef.current.map_
      .getProjection()
      .fromLatLngToPoint(marker1);
    const point2 = mapRef.current.map_
      .getProjection()
      .fromLatLngToPoint(marker2);

    const pixelSize = Math.pow(2, -mapRef.current.map_.getZoom());

    const distance =
      Math.sqrt(
        (point1.x - point2.x) * (point1.x - point2.x) +
          (point1.y - point2.y) * (point1.y - point2.y)
      ) / pixelSize;
    return distance;
  };

  const getNftWhat3words = async (top_step: number, left_step: number) => {
    // top-left
    const nftConvertToWordsOptions1: ConvertTo3waOptions = {
      coordinates: {
        lat: parsePosition(6, 56, 48.9 - 0.65 * top_step, "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * left_step, "W"),
      },
    };

    // top-right
    const nftConvertToWordsOptions2: ConvertTo3waOptions = {
      coordinates: {
        lat: parsePosition(6, 56, 48.9 - 0.65 * top_step, "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * (left_step + 1), "W"),
      },
    };

    // bottom-left
    const nftConvertToWordsOptions3: ConvertTo3waOptions = {
      coordinates: {
        lat: parsePosition(6, 56, 48.9 - 0.65 * (top_step + 1), "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * left_step, "W"),
      },
    };

    // bottom-right
    const nftConvertToWordsOptions4: ConvertTo3waOptions = {
      coordinates: {
        lat: parsePosition(6, 56, 48.9 - 0.65 * (top_step + 1), "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * (left_step + 1), "W"),
      },
    };

    const convertToWords1: any = await convertToWordsClient.run(
      nftConvertToWordsOptions1
    );

    const convertToWords2: any = await convertToWordsClient.run(
      nftConvertToWordsOptions2
    );

    const convertToWords3: any = await convertToWordsClient.run(
      nftConvertToWordsOptions3
    );

    const convertToWords4: any = await convertToWordsClient.run(
      nftConvertToWordsOptions4
    );

    setNftBoundWords({
      topLeftWhat3Words: convertToWords1.words,
      topRightWhat3Words: convertToWords2.words,
      bottomLeftWhat3Words: convertToWords3.words,
      bottomRightWhat3Words: convertToWords4.words,
    });
  };

  const showMarker = (
    top_step: number,
    left_step: number,
    nftNumber: number
  ) => {
    const iconSize = distanceInPx(
      {
        lat: parsePosition(6, 56, 48.9 - 0.65 * top_step, "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * left_step, "W"),
      },
      {
        lat: parsePosition(6, 56, 48.9 - 0.65 * (top_step + 1), "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * left_step, "W"),
      }
    );
    const icon = {
      url: "https://s2.coinmarketcap.com/static/img/coins/64x64/1.png",
      scaledSize: new google.maps.Size(iconSize, iconSize),
      origin: new google.maps.Point(0, 0),
      anchor: new google.maps.Point(0, 0),
    };

    if (marker) {
      marker.setMap(null);
    }

    const map = mapRef.current.map_;
    const iconMarker = new google.maps.Marker({
      position: new google.maps.LatLng({
        lat: parsePosition(6, 56, 48.9 - 0.65 * top_step, "N"),
        lng: parsePosition(0, 55, 37 - 0.65 * left_step, "W"),
      }),
      map,
      icon: icon,
    });
    setMarker(iconMarker);
  };

  const clickHandler = async (e: ClickEventValue) => {
    if (convertLoading) {
      return;
    }
    setConvertLoading(true);
    setCurrentPositionWhat3words("");
    resetNftData();
    const target_top = 6 * 3600 + 56 * 60 + 48.9 - e.lat * 3600;
    const target_left = 55 * 60 + 37 + e.lng * 3600;
    const top_step = Math.floor(target_top / 0.65);
    const left_step = Math.floor(target_left / 0.65);
    if (top_step >= 0 && top_step < 90) {
      const lineInfo = gridLineData[top_step];
      if (
        left_step >= lineInfo.startBlank &&
        left_step < lineInfo.number + lineInfo.startBlank
      ) {
        setSelectNftPositionTop(top_step);
        setSelectNftPositionLeft(left_step);
        const newArray = gridLineData
          .slice(0, top_step)
          .map((item) => item.number);
        const nftNumber =
          newArray.reduce((a, b) => a + b, 0) +
          left_step -
          lineInfo.startBlank +
          1;

        showMarker(top_step, left_step, nftNumber);
        getNftWhat3words(top_step, left_step);
        setMapNumber(nftNumber);
      }
    }

    const convertToWordsOptions: ConvertTo3waOptions = {
      coordinates: { lat: e.lat, lng: e.lng },
    };
    const convertToWordsRes: any = await convertToWordsClient.run(
      convertToWordsOptions
    );
    setCurrentPositionWhat3words(convertToWordsRes.words);
    // const convertToDmsOptions: ConvertToCoordinatesOptions = {
    //   words: convertToWordsRes.words,
    // };
    // const convertToDmsRes: any = await convertToDmsClient.run(
    //   convertToDmsOptions
    // );
    // if (rectArea) {
    //   rectArea.setMap(null);
    // }
    // rectangle = new google.maps.Rectangle();

    // rectangle.setOptions({
    //   strokeColor: "#FF0000",
    //   strokeOpacity: 0.8,
    //   strokeWeight: 2,
    //   fillColor: "red",
    //   fillOpacity: 0.35,
    //   map,
    //   bounds: {
    //     north: convertToDmsRes.square.northeast.lat,
    //     south: convertToDmsRes.square.southwest.lat,
    //     east: convertToDmsRes.square.northeast.lng,
    //     west: convertToDmsRes.square.southwest.lng,
    //   },
    // });
    // setRectArea(rectangle);
    setConvertLoading(false);
  };

  const changeHandler = (e: ChangeEventValue) => {
    if (mapNumber && marker) {
      showMarker(selectNftPositionTop, selectNftPositionLeft, mapNumber);
    }

    if (e.zoom > 17) {
      const gridOptions: GridSectionOptions = {
        boundingBox: {
          southwest: { lat: e.marginBounds.sw.lat, lng: e.marginBounds.sw.lng },
          northeast: { lat: e.marginBounds.ne.lat, lng: e.marginBounds.ne.lng },
        },
        format: "geojson",
      };
      gridClient
        .run(gridOptions)
        .then((res: GridSectionJsonResponse | GridSectionGeoJsonResponse) => {
          if (gridData !== undefined) {
            for (let i = 0; i < gridData.length; i++) {
              mapRef.current.map_.data.remove(gridData[i]);
            }
          }
          gridData = mapRef.current.map_.data.addGeoJson(res);
          mapRef.current.map_.data.setStyle({
            strokeColor: "#bbb",
            strokeWeight: 0.5,
          });
        });
    } else {
      for (let i = 0; i < gridData?.length; i++) {
        mapRef.current.map_.data.remove(gridData[i]);
      }
    }
  };

  const handleApiLoaded = (map: any, maps: any) => {
    const triangleCoords = [
      {
        lat: parsePosition(6, 56, 2.1, "N"),
        lng: parsePosition(0, 54, 45.3, "W"),
      },
      {
        lat: parsePosition(6, 56, 30.2, "N"),
        lng: parsePosition(0, 54, 49.5, "W"),
      },
      {
        lat: parsePosition(6, 56, 48.9, "N"),
        lng: parsePosition(0, 54, 51.9, "W"),
      },
      {
        lat: parsePosition(6, 56, 35.3, "N"),
        lng: parsePosition(0, 55, 37.0, "W"),
      },
      {
        lat: parsePosition(6, 56, 17.4, "N"),
        lng: parsePosition(0, 55, 34.5, "W"),
      },
      {
        lat: parsePosition(6, 55, 50.9, "N"),
        lng: parsePosition(0, 55, 31.0, "W"),
      },
      {
        lat: parsePosition(6, 56, 2.1, "N"),
        lng: parsePosition(0, 54, 45.3, "W"),
      },
    ];

    const bermudaTriangle = new maps.Polygon({
      paths: triangleCoords,
      strokeColor: "#FF0000",
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: "#FF0000",
      fillOpacity: 0.05,
    });
    bermudaTriangle.setMap(map);

    let gridLineGroup = [];
    for (let i = 0; i <= 90; i++) {
      let gridLineX = [
        {
          lat: parsePosition(6, 56, 48.9 - 0.65 * i, "N"),
          lng: parsePosition(0, 55, 37.0, "W"),
        },
        {
          lat: parsePosition(6, 56, 48.9 - 0.65 * i, "N"),
          lng: parsePosition(0, 54, 45, "W"),
        },
      ];
      gridLineGroup.push(gridLineX);
    }

    for (let j = 0; j <= 80; j++) {
      let gridLineY = [
        {
          lat: parsePosition(6, 56, 48.9, "N"),
          lng: parsePosition(0, 55, 37.0 - 0.65 * j, "W"),
        },
        {
          lat: parsePosition(6, 55, 50.4, "N"),
          lng: parsePosition(0, 55, 37.0 - 0.65 * j, "W"),
        },
      ];

      gridLineGroup.push(gridLineY);
    }

    const outGridLine = new maps.Polygon({
      paths: gridLineGroup,
      strokeColor: "#979797",
      strokeOpacity: 0.8,
      strokeWeight: 1,
    });
    outGridLine.setMap(map);
  };

  const nftSearchInput = (e: any) => {
    const text = e.target.value;
    if (
      text[text.length - 1].charCodeAt() < 48 ||
      text[text.length - 1].charCodeAt() > 57
    ) {
      return;
    }

    if (Number(text) > 5236) {
      setSearchNumber("5236");
      return;
    }

    setSearchNumber(e.target.value);
  };

  const nftSearch = () => {
    if (convertLoading) {
      return;
    }
    setConvertLoading(true);
    if (Number(searchNumber) === 0) {
      alert("Input correct number");
      return;
    }
    resetNftData();
    let top_step = 0,
      left_step = 0,
      sum = 0;
    for (let i = 0; i < gridLineData.length; i++) {
      sum += gridLineData[i].number;
      if (sum >= Number(searchNumber)) {
        top_step = i;
        left_step =
          Number(searchNumber) -
          sum +
          gridLineData[i].startBlank +
          gridLineData[i].number -
          1;
        setSelectNftPositionTop(top_step);
        setSelectNftPositionLeft(left_step);
        break;
      }
    }
    getNftWhat3words(top_step, left_step);
    setMapNumber(Number(searchNumber));
    showMarker(top_step, left_step, Number(searchNumber));
    setSearchNumber("");
    setConvertLoading(false);
  };

  const resetAll = () => {
    if (convertLoading) {
      return;
    }
    resetNftData();
    setCurrentPositionWhat3words("");
  };

  return (
    <React.Fragment>
      <Paper sx={{ width: "100%" }}>
        <Box
          sx={{
            position: "relative",
            width: "100%",
            height: "100vh",
          }}
        >
          <Box
            sx={{
              position: "absolute",
              top: "20px",
              left: "20px",
              width: "440px",
              padding: "12px",
              boxShadow: "0 0 5px rgb(0 0 0 / 20%)",
              background: "#fff",
              zIndex: "10",
            }}
          >
            <Box
              mb="12px"
              sx={{
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <TextField
                variant="outlined"
                placeholder="Input number"
                fullWidth
                value={searchNumber}
                onChange={nftSearchInput}
              />
              <Button
                variant="contained"
                sx={{ ml: "12px" }}
                onClick={nftSearch}
              >
                Search
              </Button>
              <Button
                variant="contained"
                sx={{ ml: "12px" }}
                onClick={resetAll}
              >
                Reset
              </Button>
            </Box>
            <Typography sx={{ mb: "8px" }}>
              <strong>Current What3Words:</strong> {currentPositionWhat3words}
            </Typography>
            <Typography sx={{ mb: "8px" }}>
              <strong>Selected Position Number:</strong> {mapNumber}
            </Typography>
            <Typography sx={{ mb: "8px" }}>
              <strong>Top-Left:</strong> {nftBoundWords.topLeftWhat3Words}
            </Typography>
            <Typography sx={{ mb: "8px" }}>
              <strong>Top-Right:</strong> {nftBoundWords.topRightWhat3Words}
            </Typography>
            <Typography sx={{ mb: "8px" }}>
              <strong>Bottom-Left:</strong> {nftBoundWords.bottomLeftWhat3Words}
            </Typography>
            <Typography sx={{ mb: "8px" }}>
              <strong>Bottom-Right:</strong>{" "}
              {nftBoundWords.bottomRightWhat3Words}
            </Typography>
          </Box>
          {mapNumber && (
            <Box
              sx={{
                position: "absolute",
                top: "350px",
                left: "20px",
                width: "300px",
                height: "300px",
                padding: "4px",
                boxShadow: "0 0 5px rgb(0 0 0 / 20%)",
                background: "#fff",
                zIndex: "10",
              }}
            >
              <Box
                component="img"
                src="https://s2.coinmarketcap.com/static/img/coins/64x64/1.png"
                alt=""
                sx={{
                  width: "100%",
                  height: "100%",
                }}
              />
            </Box>
          )}
          <GoogleMapReact
            yesIWantToUseGoogleMapApiInternals={true}
            ref={mapRef}
            onClick={clickHandler}
            onChange={changeHandler}
            onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
            bootstrapURLKeys={{
              key: "AIzaSyDq38-QJCuQZk8-QoTeuLO-diT-HCPohCA",
            }}
            defaultCenter={{
              lat: 6.93886,
              lng: -0.919847,
            }}
            defaultZoom={16}
          ></GoogleMapReact>
        </Box>
      </Paper>
    </React.Fragment>
  );
};

export default GoogleMap;
